}

.info {
    text-align: left;
    padding-top: 10px;
    margin-bottom: 10px;
    position: relative;
    font-size: 1.5rem;
    padding-left: 10px;
}

.info b {
    margin-right: 5px;
}

.details {
    display: inline-block;
    /* width: 40%;
    overflow: hidden;
    text-overflow: ellipsis;
    top: 32px;
    position: absolute; */
    display: inline-block;
    position: absolute;
    height: 22px;
    width: 69%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.details:hover {
    overflow: visible;
    /* background-color: white; */
    width: auto;
}

